@import-normalize;

html {
	background-color: #cde;
	font-family: sans-serif;
	font-size: 4vmin;
}

body {
	margin: 0;
}

input,
button {
	font-size: 1rem;
	border-radius: 4vmin;
	border: 0.2vmin solid #678;
	text-align: center;
	padding: 1vmin 2vmin;
}

input {
	background: linear-gradient(to bottom, #def 0%, #fff 50%);
	width: 10vmin;
}

input:invalid {
	border-color: #bf0000;
	background: linear-gradient(to bottom, #ffdddd 0%, #fff 50%);
}

input:invalid:focus {
	border-color: #830000;
}

input:focus,
button:focus {
	outline: 0;
	border-color: #004ba1;
}

button {
	background-color: #ffffff7e;
	box-shadow: inset 0.4vmin 0.4vmin 0.4vmin 0 white;
}

button:hover,
button:focus {
	background-color: #ffffffc7;
}

button:active {
	background-color: #0000007e;
	box-shadow: inset -0.4vmin -0.4vmin 0.4vmin 0 white;
}

:local(.app) {
	padding: 4vmin;
	text-align: center;
}
